$color-black: #000000;
$color-white: #ffffff;

a {
    text-decoration: none;
    color: $color-white;
}

body {

    font-family: 'Oxygen', sans-serif;
    // background-image: linear-gradient(to top, rgba($color-black, 0.9), rgba($color-black, 0.9)), url('/img/developer.jpg');
    background-image: linear-gradient(to bottom right, #100718, #0a1318 );
    background-size: cover;
    height: 100vh;
    overflow: hidden;
    background-repeat: no-repeat;

    @media screen and (max-width: 767px) {
        font-size: 10px;
    }

    .hero {
        height: calc(100vh - 80px);
        width: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        flex-direction: column;
        overflow: hidden;
        color: $color-white;

        @media screen and (max-width: 767px) {
            height: calc(100vh - 50px);
        }

        &__name {
            font-size: 32px;
            margin-block-start: 0;
            margin-block-end: 0;
        }

        &__title {
            font-size: 120px;
            margin-block-start: 0;
            margin-block-end: 0;

            @media screen and (max-width: 767px) {
                font-size: 40px;
            }
        }
    }

    .social {
        color: $color-white;
        display: flex;

        a:last-child {

            .social__button {
                margin-right: 0;
            }
        }


        &__button {

            display: flex;
            justify-content: center;
            align-items: center;
            background: #222222;
            color: $color-white;
            cursor: pointer;
            margin-right: 15px;
            border-radius: 5px;
            padding: 10px 15px;
            width: 40px;

            &:hover {
                background: #111111;
            }

            img {
                height: 25px;
            }
        }
    }

    .menu {
        
        display: flex;
        justify-content: space-between;
        opacity: 0;
        position: fixed;
        bottom: 0;
        width: auto;
        padding: 15px;
        text-transform: uppercase;
        letter-spacing: 0.25em;
        height: 50px;

        @media screen and (max-width: 767px) {
            width: 100%;
            padding: 0;
            font-size: 9px;
            letter-spacing: 0.15em;
        }

        &__button {
            flex: 1 0 150px;
            padding: 10px 15px;

            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #222222;
            color: $color-white;
            cursor: pointer;
            margin-right: 15px;
            border-radius: 5px;

            @media screen and (max-width: 767px) {
                margin-right: 0;
                border-radius: 0;
                border: 1px solid $color-white;
                flex: 1 1 25%;
                padding: 5px;
            }

            &--home {
                flex: 0 0 40px;

                img {
                    height: 25px;
                }
            }

            &:hover {
                background: #111111;
            }

            &.active {
                background: $color-black;
            }
        }
    }

    .resume {
        height: calc(100vh - 50px);
        display: flex;
        overflow: hidden;
        color: $color-white;

        &__slider {
            margin: auto;
            width: 100%;
    
            &__entry {
                padding: 15px;
                background: $color-black;

                &__container {
                    background: rgba($color-white, 0.9);
                    color: $color-black;
                    padding: 15px;
                }

                &__list {
                    padding-inline-start: 20px;
                }
    
                @media screen and (max-width: 767px) {
                    padding: 10px;
                }
    
                &:focus {
                    outline: none !important;
                }

                &__line {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    &__position, &__location, &__tech {
                        font-style: italic;
                    }
                }

                &__link {
        
                    display: flex;
                    align-items: center;
        
                    .icon {
                        height: 15px;
                    }
                }

                &__list {
                    &:last-child {
                        margin-bottom: 0.45em;
                    }
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background: $color-black;
                    opacity: 0.8;
                    transition: opacity 1s ease;
                }
            }
        }
    }

    .portfolio {
        height: calc(100vh - 50px);
        display: flex;
        overflow: hidden;
        &__slider {

            margin: auto;
            width: 100%;

            &__vid {
                padding: 15px;
                background: black;
                position: relative;
                
                &:focus {
                    outline: none !important;
                }

                &:hover video {
                    opacity: 1;
                }

                img {
                    width: 100%;
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background: $color-black;
                    opacity: 0.8;
                    transition: opacity 1s ease;
                    z-index: 1;
                }

                @media screen and (max-width: 767px) {
                    padding: 10px;
                }

                &:focus {
                    outline: none !important;
                }

                video {
                    width: calc(100% - 30px);
                    position: absolute;
                    left: 15px;
                    top: 15px;
                    opacity: 0;
                    transition: opacity 0.5s ease;

                    @media screen and (max-width: 767px) {
                        display: none;
                    }
                }
            }
        }
    }

    .skills {
        height: calc(100vh - 50px);
        display: flex;
        overflow: hidden;
        color: $color-white;

        &__slider {
            margin: auto;
            width: 100%;
    
            &__entry {
                padding: 15px;
                background: $color-black;
                &:focus {
                    outline: none !important;
                }

                &__container {
                    background: rgba($color-white, 0.9);
                    color: $color-black;
                    padding: 15px;
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background: $color-black;
                    opacity: 0.8;
                    transition: opacity 1s ease;
                }
            }
        }
    }

    .slick-slider {
        .slick-prev {
            left: 25px;
            width: 40px;
            height: 40px;
            z-index: 1;

            @media screen and (max-width: 767px) {
                left: 0px;
            }
        }

        .slick-next {
            width: 40px;
            height: 40px;
            right: 25px;
            z-index: 1;

            @media screen and (max-width: 767px) {
                right: 0px;
            }
        }
        .slick-slide {
            transform: scale(0.8);
            transition: transform 1s ease;
            
            @media screen and (max-width: 767px) {
                transform: scale(0.9);
            }
            
            & > div {
                display: flex;
            }
        }

        .slick-center {
            transform: scale(1.0);

            .resume__slider__entry {
                &::after {
                    opacity: 0.1;
                }
            }
            .portfolio__slider__vid {
                &::after {
                    opacity: 0;
                }
            }
            .skills__slider__entry {
                &::after {
                    opacity: 0.1;
                }
            }
        }

        .slick-list {
            transform: translate(100%);
            transition: transform 1s ease;

            .slick-track {
                display: flex;
                align-items: center;
            }
        }
    }
}